<template>
    <d2-container>
        <!-- 用户管理 -->
        <!-- header -->
        <template slot="header">
            <el-row>
                <el-col :span="24" style="margin-right:10px;">
                    <!-- <el-autocomplete class="inline-input" v-model="state1" :fetch-suggestions="querySearch" placeholder="请输入内容"></el-autocomplete> -->
                    <el-alert title="用户管理" type="info" :closable="false">
                    </el-alert>
                </el-col>
                <el-col :span="5">
                    <!-- <el-button type="primary" plain>查询</el-button> -->
                    <!-- <el-button type="primary" @click="addrole" plain>添加</el-button> -->
                </el-col>
            </el-row>
        </template>
        <el-table :data="tableData" border style="width: 100%" >
            
            <el-table-column type="index" width="50">
            </el-table-column>
            <el-table-column prop="name" label="用户名" width="180">
            </el-table-column>
            <el-table-column prop="nickname" label="昵称" width="180">
            </el-table-column>
            <el-table-column prop="role[0].display_name" label="权限" width="180">
            </el-table-column>
            <el-table-column prop="phone" label="手机号">
            </el-table-column>
            <el-table-column prop="sex" label="性别 0 == 女 | 1 == 男 ">
            </el-table-column>
            <el-table-column prop="email" label="email">
            </el-table-column>
            <!-- <el-table-column label="操作">
                <template scope="val">
                    <el-button type="primary" plain>编辑</el-button>
                    <el-button type="primary" plain>详情</el-button>
                    <el-button type="primary" plain>删除</el-button>
                </template>
            </el-table-column> -->
        </el-table>
        <!-- footer -->
        <el-row type="flex" justify="space-between" align="middle" style="height: 80px;">
            <el-col :span="24">
                <!-- <el-checkbox v-model="checked" style="margin: 0 10px;"></el-checkbox> -->
                <!-- <el-button type="danger" :disabled="checked">批量删除</el-button> -->
                <el-pagination layout="prev, pager, next" @current-change="page" :page-size="10" :total="checked" style="float: right;">
                </el-pagination>
            </el-col>
        </el-row>

        <!-- 添加角色 -->
        <!-- <el-dialog title="提示" :visible.sync="centerDialogVisible" width="30%" center>
            <el-form ref="form" :model="form" label-width="80px">
                <el-form-item label="角色名称">
                    <el-input v-model="form.name" placeholder="请输入角色名称"></el-input>
                </el-form-item>
                <el-form-item label="角色身份">
                    <el-select v-model="form.region" placeholder="请选择角色身份">
                        <el-option label="管理员" value="管理员"></el-option>
                        <el-option label="开发" value="开发"></el-option>
                        <el-option label="开发2" value="开发2"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="角色描述">
                    <el-input type="textarea" resize="none" :rows="2" placeholder="请输入角色描述" v-model="form.textarea">
                    </el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="centerDialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="onSubmit">确 定</el-button>
            </span>
        </el-dialog> -->

    </d2-container>
</template>

<script>
    import { roles, add_roles,list } from '../../plugin/axios/api.js';
    import qs from 'qs';
    export default {
        name: 'role',
        data() {
            return {
                num: 10,
                value3: '',
                state1: '',
                checked: 0,
                pages:1,
                centerDialogVisible: false,
                multipleSelection: [],
                tableData: [],
                textarea: '',
                form: {
                    name: '',
                    region: '',
                    textarea: '',
                }
            }
        },
        methods: {
            querySearch(queryString, cb) {
                var restaurants = this.restaurants;
                var results = queryString ? restaurants.filter(this.createFilter(queryString)) : restaurants;
                // 调用 callback 返回建议列表的数据
                cb(results);
            },
            createFilter(queryString) {
                return (restaurant) => {
                    return (restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
                };
            },
            handleSelect(item) {
                console.log(item);
            },
            toggleSelection(rows) {
                if (rows) {
                    rows.forEach(row => {
                        this.$refs.multipleTable.toggleRowSelection(row);
                    });
                } else {
                    this.$refs.multipleTable.clearSelection();
                }
            },
            page(a){
                this.pages = a;
                this.initrole()
            },
            handleSelectionChange(val) {
                console.log(val);

                this.multipleSelection = val;
            },
            addrole() {//添加角色
                this.centerDialogVisible = true;

            },
            onSubmit() {//添加角色
                console.log('submit!');
                if (this.form.name == '') {
                    this.$message({
                        message: '请输入用户姓名',
                        center: true,
                        type: 'error'
                    });
                    return
                }
                if (this.form.region == '') {
                    this.$message({
                        message: '请选择用户身份',
                        center: true,
                        type: 'error'
                    });
                    return
                }
                let arr = qs.stringify({
                    name: this.form.name,
                    display_name: this.form.region,
                    description: this.form.textarea
                })
                add_roles(arr).then(res => {
                    console.log(res);
                    if (res.status) {
                        let str = '';
                        for (const key in res.data.errors) {
                            console.log(key);
                            
                            str+=res.data.errors[key][0]+' '
                        }
                        this.$message({
                            message: res.status+' '+str,
                            center: true,
                            type: 'error'
                        });
                        return
                    }
                })
                console.log(this.form);

                // this.centerDialogVisible = false;
            },
            initrole() {
                let par = {
                    page: this.pages,
                    limit: 10
                }
                list(par).then(res => {
                    console.log(res);
                    this.tableData = res.data.data;
                    this.checked = res.data.paginate.total;
                })
            },
            selec(a) {
                console.log(a);

            }
        },
        mounted() {
            this.initrole()
        },
        watch: {
        }
    }
</script>
